.app {
  margin: 0 auto;
  /* max-width: 1400px; */
}

.fadeIn {
  animation: 0.3s fadeIn forwards;
}

.fadeOut {
  animation: 0.3s fadeOut forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0.1;
    transform: translate(-5px, 0);
  }
  to {
    opacity: 1;
    transform: translate(0px, 0px);
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: translate(0px, 0px);
  }
  to {
    transform: translate(-5px, 0);
    opacity: 0.1;
  }
}
